import flowers from "../data";
import Figure from "react-bootstrap/Figure";
import "bootstrap/dist/css/bootstrap.css";
import { Link } from "react-router-dom";

function Home() {
  return (
    <div>
      <h1>Home</h1>
      {createFlowers()}
    </div>
  );
}

function createFlowers() {
  function createQuestions(name) {
    let temp = [];

    let pos = flowers
      .map(function (e) {
        return e.name;
      })
      .indexOf(name);
    if (pos > flowers.length - 7) {
      pos = flowers.length - 7;
    }
    for (var i = pos; i < pos + 6; i++) {
      var count = 3;
      var options = [];
      options.push(flowers[i]);

      while (count > 0) {
        var randomIndex = Math.floor(Math.random() * flowers.length);
        var randomFlower = flowers[randomIndex];
        if (!options.includes(randomFlower.name)) {
          options.push(randomFlower);
          count--;
        }
      }
      var flower = { ...flowers[i], options };
      temp.push(flower);
    }
    return temp;
  }
  var images = [];
  for (var flower of flowers) {
    images.push(
      <Figure>
        <Link
          to={{
            pathname: "/quiz",
            state: {
              answer: flower.name,
              image: flower.picture,
              questions: createQuestions(flower.name),
            },
          }}
        >
          <Figure.Image
            width={200}
            height={200}
            alt={flower.name}
            src={flower.picture}
          />
          <Figure.Caption>{flower.name}</Figure.Caption>
        </Link>
      </Figure>
    );
  }
  return images;
}

export default Home;
