import flowers from "../data";
import React, { useState, useEffect } from "react";
import "bootstrap/dist/css/bootstrap.css";

function Quiz(props) {
  const [score, setScore] = useState(0);
  const [questionsTried, setQuestionsTried] = useState([]);  
  useEffect(() => {
    if (score === 6) {
      alert("You won!");
    }
  }, [score]);
  let clicker = (selectedName, answer, index) => {
    if (score !== 6 && selectedName === answer) {
      setScore(score + 1);
      let x = questionsTried;
      x.push(index.toString());
      setQuestionsTried(x);
      console.log(questionsTried);
    }
  };
  let isTried = (index) => {
  	let found = questionsTried.find(x => x === index.toString());
  	console.log(index);
  	console.log(found);
  	if (found){
  		return true;
  	}
  	else{
  		return false;
  	}
  }
  return (
    <div>
      <h1>Quiz</h1>
      <h2>{score} / 6</h2>
      {props.location.state.questions.map((question, index) => {
        return (
          <Question
            name={question.name}
            img={question.picture}
            options={question.options}
            clicker={clicker}
            index={index}
            isTried={isTried}
          />
        );
      })}
    </div>
  );
}
function Question({ name, img, options, clicker, index, isTried}) {
  return (
    <div>
      <img src={img} />
      {isTried(index) ? "" : options.map((option) => {
        return (
          <button onClick={() => clicker(option.name, name, index) }>
            {option.name}
          </button>
        );
      })}
    </div>
  );
}
export default Quiz;
